
import './App.css';
import data from "./data/november2020.csv";
import { csv } from "d3";
import { useEffect, useState } from 'react';
import { Bar, HorizontalBar } from 'react-chartjs-2';

function App() {

  const [chart, setChart] = useState()

  useEffect(()=>{
    csv(data).then(result=>{
      let count = 0, day = 0, days = [], kesBaru = [], countBaru = 0
      console.log(result)
      result.forEach(row=>{
        count++
        countBaru = countBaru ? parseInt(row.kesPositifBaru) : countBaru+parseInt(row.kesPositifBaru)
        if(count % 12 == 0){
          kesBaru[day] = countBaru
          countBaru = 0
          days.push(`${day+1}/11`)
          day++
        }
      })
      console.log(kesBaru)
      setChart({
        labels: days,
        datasets: [
          {
            label: 'Jumlah Kes Positif',
            data: kesBaru,
            backgroundColor: 'rgba(255,99,132,0.7)',
          }
        ]
      })
    })
  }, [])

  //setiap 12 row ialah satu tarikh (0-11 -> 1 haribulan)

  const options = {
    scales: {
        yAxes: [{
            ticks: {
                beginAtZero: true,
                min: 0,
                callback: function(value) {if (value % 1 === 0) {return value;}}
            }
        }]
    },
    title:{
      text:'Kes Positif COVID-19 di Sarawak bulan November', 
      display:true
    }
  }

  return (
    <div className="App">
      <div className='bar'>
        {chart ? <Bar data={chart} options={{...options,}} /> : 'loading data ....'}
      </div>
    </div>
  );
}

export default App;
